
.site-head{
	width: 100%;
	height: 93px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    .nav-bar{
        height: 100%;
        max-width: 1440px;
        padding: 28px 40px;
        margin: 0 auto;
        text-align: center;
        @include mobile{
            padding: 20px 15px;
        }
    }
}
.logo-link{
    float: left;
    img{
        vertical-align: middle;
    }
}
.nav-list-wrapper{
    display: inline-block;
    list-style: none;
    vertical-align: middle;
    @include tablet{
        display: none;
    }
    .nav-list{
        display: inline-block;
        margin: 0 20px;
        &-link{
            color: $header-text-color;
            font-family: $font;
            font-size: 14px;
            font-weight: bold;
            text-decoration: none;
            line-height: 2.8;
        } 
        @include tablet{
            vertical-align: middle;
            width: 30%;
            margin: auto;
            display: list-item;
            list-style-type: none;
            padding: 20px;
        }   
    }
}
@include tablet{
    .nav-list-wrapper.show{
        display: block;
        position: absolute;
        z-index: 5;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        padding: 100px 0;
        margin: 0;
        text-align: center;
        background-color:  #008cbe;
        opacity: 0.8;
    }
}
.nav-button{
    float: right;
    margin: 7px;
}
.search-button{
    color: $header-text-color;
    margin: 0 20px;
    font-size: 18px;
    float: right;
    margin: 7px 20px;
    @include mobile{
        margin: 7px 10px;;
    }
}
.hamburger-icon{
    background-color: transparent;
    color: #ffffff;
	font-size: 28px;
	border: 0;
	float: right;
	margin: 0 20px;
	display: none;
	cursor: pointer;
    position: relative;
    z-index: 10;

    &:focus{
	outline: none;
    }
    @include tablet{
        display: block;
    }
    @include mobile{
        margin: 0 10px;;
    }
}